___CSS_LOADER_EXPORT___.push([module.id, `*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

body {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

h1 {
  font-family: 'montserrat', sans-serif;
  font-weight: 700;
  font-size: 40px;
  color: white;
}

h2 {
  font-family: 'montserrat', sans-serif;
  font-weight: 600;
  margin-bottom: 10px;
  color: white;
  font-size: 25px;
}

.main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  height: 100vh;
  overflow: hidden;
}

.main-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 80%;
  gap: 20px;
}

.recent-score-container {
  display: flex;
  flex-direction: column;
  width: 60%;
}

.recent-score {
  display: flex;
  flex-direction: row;
  gap: 20px;
}

.score-box {
  border: 3px solid black;
  height: 300px;
  overflow-y: scroll;
}

.add-score {
  display: flex;
  flex-direction: column;
  width: 40%;
}

.refresh-button {
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  cursor: pointer;
}

#input-text,
#input-number {
  padding: 10px;
  border-radius: 20px;
  outline: none;
  margin-bottom: 5px;
}

.form {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  margin-bottom: 20px;
}

.submit-btn-parent {
  display: flex;
  flex-direction: row;
  justify-content: right;
}

.submit-button {
  padding: 10px;
  border-radius: 15px;
  width: 120px;
  cursor: pointer;
  border: 0.5px solid rgba(0, 230, 64);
}

.user-scores-listed {
  padding: 10px;
  color: white;
  font-family: 'montserrat', sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.user-scores-listed:nth-child(even) {
  background-color: rgba(0, 177, 106);
}

.user-scores-listed:nth-child(odd) {
  background-color: rgba(30, 130, 76);
}

@media screen and (min-width: 600px) {
  .h1-text {
    margin-right: 370px;
  }
}

@media screen and (min-width: 1000px) {
  .h1-text {
    margin-right: 550px;
  }
}

@media screen and (min-width: 1200px) {
  .h1-text {
    margin-right: 800px;
  }
}
